import React from 'react';
import { Link as ScrolledLink } from "react-scroll";

function Header(props) {
  return (
    <header className="header">
      <ul className={`header__links`}>
        <li className='header__link'>
          <ScrolledLink to={'about'} spy={true} smooth={true} duration={500}> </ScrolledLink>
        </li>
        <li className='header__link'>
          <ScrolledLink to={'story'} spy={true} smooth={true} duration={500}></ScrolledLink>
        </li>
        <li className='header__link'>
          <ScrolledLink to={'ddd'} spy={true} smooth={true} duration={500}>3</ScrolledLink>
        </li>
        <li className='header__link'>
          <ScrolledLink to={'contacts'} spy={true} smooth={true} duration={500}></ScrolledLink>
        </li>
      </ul>

    </header>
  );
}

export default Header;